.container {
  display: grid;
  background-color: #85869378;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 0;
  height: 100%;
  -webkit-box-shadow: -1px 3px 13px 7px rgba(0, 0, 0, 0.47);
  box-shadow: -1px 3px 13px 7px rgba(0, 0, 0, 0.47);
}

#item-0 {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #e0e0e0;
  background-color: #858693;
  grid-row-start: 1;
  grid-column-start: 1;
  font-size: 4rem;
  grid-row-end: 2;
  grid-column-end: 5;
}

#item-1 {
  background-color: #e0e0e0;
  grid-row-start: 2;
  grid-column-start: 1;
  grid-row-end: 3;
  grid-column-end: 2;
}

#item-2 {
  background-color: #e0e0e0;
  grid-row-start: 2;
  grid-column-start: 2;
  grid-row-end: 3;
  grid-column-end: 3;
}

